import React, { useRef, useEffect, useCallback, useState} from 'react';
import SporeEmbed from 'components/player/SporeEmbed.js';
import './video.scss';
import '../nft-jacker/nft-jacker.scss';
import '../drops/drop.scss';


import {connect} from 'react-redux';

const DURATION = 232;

const VideoJacker = ({videoURL="https://zequencer.mypinata.cloud/ipfs/Qma9nqqcyTm4c9J72Hu8DQbqjVthgG6X31xMQBAfDbo5qo", playerURL="https://zequencer.mypinata.cloud/ipfs/Qmc7Wd3EpdcWvgJLPpNGb2TpZAJ62xMxTmVTBXNf1Eao2p/?tokenId=24&gallery=true"}) => { 
    let ref = useRef();
    let baseBPM = useRef();

    useEffect(() => {
        window.addEventListener("message", onMessage, false);
    }, []);

    const onMessage = useCallback((e) => {
        if (e.data.type === "jacker-playback-status") {
            let playing = parseInt(e.data.body);
            if (playing) {
                ref.current.play();
            } else {
                ref.current.pause();
            }
        }
        if (e.data.type === "song-player-progress") {
            let progress = parseFloat(e.data.body);
            let x = progress/100
            let sec = DURATION * x;
            ref.current.currentTime = sec;
        }
        if (e.data.type === "song-bpm") {
            let bpm = e.data.body;
            baseBPM.current = parseFloat(bpm);
            
        }
        if (e.data.type === "playback-bpm") {
            let bpm = e.data.body;
            if (baseBPM.current) {
                let playbackRate = bpm / baseBPM.current;
                ref.current.playbackRate = playbackRate;
            }
        }
    }, []);

    return (<div className="drop nft-jacker jacked video-jacker">
              <SporeEmbed
                songTitle="VERIFIED FEAT. CHIIEF DIIN" 
                artist="HALEEK MAUL"
                url={playerURL}/>
              <video
                ref={ref}
                muted
                playsInline
                src={videoURL}/>
            <div className="drop-description">
              <p>
                <span className="purple-gradient">SPORES</span> GROWS UNIQUE STRAINS OF SONGS.
              </p>
              <p>
                IT'S WHERE MEDIA IS <span className="green-gradient">MALLEABLE</span> AND <span className="green-gradient">DYNAMIC</span>
              </p>
              <p>
            AND <span className="orange-gradient">FANS</span> BECOME INSTANT <span className="orange-gradient">CO-CREATORS</span>.
              </p>
              <p>
                WHERE OUR SHARED <span className="blue-gradient">HISTORY</span> IS PRESERVED <span className="blue-gradeint">ON-CHAIN</span>
              </p>
              <p>
                AND VALUE <span className="orange-gradient">FAIRLY DISTRIBUTED</span>.
              </p>
            </div>
            </div>
           );
};

const mapStateToProps = (state, props) => {
  return {
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(VideoJacker);
